<script>
import ModalAddDashboard from './ModalAddDashboard.vue';
import ModalAddReport from './ModalAddReport.vue';
import DataService from '../../services/DataService';
import ReportLayout from './ReportLayout.vue';
import daterange from './DateRange.vue';
export default {
    components: {
        ModalAddDashboard,
        ModalAddReport,
        ReportLayout,
        daterange
    },
    data: function () {
        return {
            isLoading: false,
            showAdddashboard: false,
            showAddReport: false,
            dashsbords: [],
            dashboardid: '0',
            reports: [],
            showFilters: false,
            showpopup: false,
            StateList: [],
            fromDate: null,
            toDate: null,
            clientByCompany: [],
            selectedClient: [],
            isDropdownOpen: false
        }
    },
    created: function () {
        this.GetDashboard();
        this.GetStateListByCountryCode();
        this.GetParentClientByCompanyIdForGroup_New();
    },
    methods: {
        updateSelectedOptions() {
            this.selectedClient = this.clientByCompany.filter(option => option.selected);
        },
        SetDateRange(from, to) {
            this.fromDate = from;
            this.toDate = to;
            this.GetDashboard();
        },
        OpenDatePopUp(event) {
            this.showpopup = !this.showpopup;
            $('#DivTimer_global').toggleClass('d-none');
        },
        openFilters() {
            this.showFilters = !this.showFilters;
        },
        opendashborad() {
            this.showAdddashboard = true;
        },
        openReport() {
            this.showAddReport = true;
        },
        GetDashboard: function () {
            var vm = this;
            DataService.GetDashboard().then(response => {
                vm.dashsbords = response.data;
                if (vm.dashsbords.length) {
                    vm.dashboardid = vm.dashsbords[0].value;
                    vm.onchangeView();
                }
            });
        },
        onchangeView: function () {
            var vm = this;
            var url = `dashboardId=${vm.dashboardid}`
            DataService.GetDashboardReport(url).then(function (response) {
                var data = response.data;
                vm.reports = [];
                setTimeout(() => {
                    vm.reports = data;
                }, 100);

            });

        },
        GetStateListByCountryCode: function () {
            var vm = this;
            var url = `countryId=223&includeCandaState=true`
            DataService.GetStateListByCountryCode(url).then(response => {
                vm.StateList = response.data;
            });
        },
        GetParentClientByCompanyIdForGroup_New: function () {
            var vm = this;
            DataService.GetParentClientByCompanyIdForGroup_New().then(response => {
                vm.clientByCompany = response.data;
            });
        },
        toggleMenu(event) {
            $(event.target).parent().parent().find('.dropdown-menu').toggleClass('d-block');
        }
    }

}

</script>
<template>
    <section class="page-action">
        <div class="container-fluid px-0">
            <div class="row">
                <input type="hidden" value="@titleName" id="hdnModuleName" />
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition p-actions-expand" style="overflow:visible;">
                        <span title="dashbord" class="p-name text-white px-2">
                            <select class="form-control customized_ddl" name="dashsbords" v-model="dashboardid"
                                v-on:change="onchangeView($event)">
                                <option v-for="item in dashsbords" v-bind:value="item.value" :key="item">{{ item.name }}
                                </option>
                            </select>
                        </span>
                        <span class="p-actions  float-right">
                            <a href="javascript:;" class="p-action-btn text-white link" @click="openFilters"><i
                                    class="fa fa-filter" aria-hidden="true"></i><br> {{ $t('Filters') }} </a>
                            <a href="javascript:;" class="p-action-btn text-white" id="teamview" @click="opendashborad"><i
                                    class="fa fa-dashboard" aria-hidden="true"></i><br>{{ $t('Add Dashboard') }}</a>
                            <a href="javascript:;" class="p-action-btn text-white" id="teamview" @click="openReport"><i
                                    class=" fa fa-plus" aria-hidden="true"></i><br>{{ $t('AddReport') }}</a>
                            <a href="javascript:;" class="p-action-btn text-white" id="teamview"
                                @click="OpenDatePopUp($event)"><i class="fa fa-calendar" aria-hidden="true"></i><br>{{
                                    $t('This Weak') }}</a>
                            <daterange pgaction="global" :showDateRange="showpopup" @set-date="SetDateRange"></daterange>
                        </span>


                        <span :class="{ 'action-list-box2 expand': true, 'd-none': showFilters == false }">

                            <span class="p-2">
                                <Multiselect placeholder="Select State" :searchable="true" :options="StateList"
                                    track-by="value" label="name" />
                            </span>
                            <span class="p-2">
                                <div class="dropdown" @click="isDropdownOpen = !isDropdownOpen">
                                    <div class="selected-options">
                                        <span v-if="selectedClient.length === 0" role="button"
                                            class="form-control dropdown-toggle" data-toggle="dropdown"
                                            @click="toggleMenu($event)">Select options</span>
                                        <span v-else-if="selectedClient.length === 1" role="button"
                                            class="form-control dropdown-toggle" data-toggle="dropdown"
                                            @click="toggleMenu($event)">{{ selectedClient[0].text }}</span>
                                        <span v-else role="button" class="form-control dropdown-toggle"
                                            data-toggle="dropdown" @click="toggleMenu($event)">{{ selectedClient.length }}
                                            options selected</span>
                                    </div>
                                    <div class="dropdown-menu" role="menu" aria-labelledby="dLabel" v-show="isDropdownOpen">
                                        <div class="option" v-for="option in clientByCompany" :key="option.value">
                                            <label>
                                                <input type="checkbox" :value="option.value" v-model="option.selected"
                                                    @change="updateSelectedOptions">
                                                {{ option.text }}
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </span>

                            <span class="p-2">
                                <select class="form-control">
                                    <option value="">{{ $t('Biller') }} {{ $t('Assigned') }}</option>
                                </select></span>

                            <div class="row flex-row-reverse mt-2">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch"
                                        class="btn btn-success formbtn text-white mr-1">
                                        <i class="fa fa-search"></i></a>
                                    <a href="javascript:;" :title="$t('Reset')" id="aClear"
                                        class="btn btn-danger formbtn widthhalf btnCancel">
                                        <i class="fa fa-refresh"></i></a>

                                </div>
                            </div>
                        </span>

                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                        </span>
                    </div>
                    <ReportLayout v-if="reports.length>0 && fromDate" @refresh-data="GetDashboard" :reports="reports" :fromDate="fromDate" :toDate="toDate"></ReportLayout>
                </div>
            </div>

            <ModalAddDashboard v-if="showAdddashboard"></ModalAddDashboard>
            <ModalAddReport v-if="showAddReport" :dashboardid="dashboardid"></ModalAddReport>
        </div>
        <loader :is-visible="isLoading"></loader>
    </section>
</template>

<style>
.action-list-box2 {
    position: absolute;
    top: 53px;
    right: 145px;
    width: 400px;
    overflow: hidden;
    display: inline-block;
    padding: 5px;
    white-space: nowrap;
    z-index: 99;
    display: block;
    background: #fff;
    border: 1px solid #bcbcbc;
}

.action-list-box2 span {
    display: block;
}</style>