<template>   
    <div>
   <div id="modalprojectquickviewpop" role="dialog" class="modal my-popups right fade" v-cloak>
        <div role="document" class="modal-dialog mw-1100">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" v-on:click="CloseGraphOptions()">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 id="myModalLabel" class="modal-title">{{ $t("Widget Setting") }}</h4>
                    <span class="user-guide">
                        <a class="ancuserguide" data-toggle="collapse" href="#collapseReplyUG" role="button"
                            aria-expanded="false" aria-controls="collapseExample">{{ $t("GuideForUser") }}</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con">{{ $t("MangeViewUG1") }}</div>
                            </div>
                        </div>
                    </span>
                </div>
                <div class="modal-body p-3">
                    <loader :is-visible="isLoading"></loader>
                    <div class="container-fluid tab-content">
                        <Form v-slot="{ values, meta }" ref="form">
                            <div class="row">
                                <div class="col-sm-12 col-lg-6 float-left leftsideicon pl-0 py-3 border">
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>{{ $t('ReportTitle') }} :</label>
                                            <input v-model="reportTitle" placeholder="Report Title" class="form-control"
                                                type="text" maxlength="50" disabled />
                                        </div>
                                    </div>
                        <div class="col-lg-12">
                                        <div class="form-group">
                                            <Field name="ChartTypes" :rules="{ 'required': true }" label="Chart Type"
                                                v-slot="{ field, errorMessage, meta }">
                                                <label>{{ $t('ChartTypes') }}<span class="text-danger">*</span></label>
                                                <div class="user_type_choice coloredicons row"
                                                    v-bind:class="{ '': true, 'is-invalid': meta.touched && !meta.valid && !selectedChartTypeID }">
                                                    <div class="btn-group btn-group-toggle mb-2" data-toggle="buttons" v-for="(item, index) in chartTypes"
                                                    :key="index">
                                                        <label
                                                            v-bind:class="{ 'btn btn_cstm': true, 'active': (item.id == selectedChartTypeID) }"
                                                            v-bind:for="'cls_usertype' + index">
                                                            <input v-bind="field" type="radio" v-bind:value="item.id"
                                                                name="cls_usertype" v-bind:id="'cls_usertype' + index"
                                                                v-model="selectedChartTypeID" />
                                                            <span class="d-block pb-2">
                                                                <em :class="[item.chart_Type == 'Pie' ? 'fa fa-pie-chart bg-transparent' : '',
                                                                item.chart_Type == 'Bar' ? 'fa fa-bar-chart bg-transparent' : '',
                                                                item.chart_Type == 'Line' ? 'fa fa-line-chart bg-transparent' : '',
                                                                item.chart_Type == 'Donut' ? 'fa fa-circle-o-notch bg-transparent' : '']"
                                                                    aria-hidden="true"></em>
                                                            </span>
                                                           
                                                        </label>
                                                    </div>
                                                </div>
                                                <span name="ChartTypes" v-if="meta.touched && !meta.valid"
                                                    class="invalid-feedback">
                                                    {{ errorMessage }}
                                                </span>
                                            </Field>
                                        </div>
                                    </div>
                     
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <Field name="selectedChartXAxis" :rules="{ 'required': true }" v-model="selectedChartXAxis"
                                                label="X Axis" v-slot="{ field, errorMessage, meta }">
                                                <label>{{ $t('XAXIS') }}<span class="text-danger">*</span></label>
                                                <div class="input-group">
                                                    <select v-bind="field" 
                                                        v-bind:class="{ 'form-control': true, 'is-invalid': meta.touched && !meta.valid && !selectedChartXAxis }">
                                                        <option value="" disabled>Select</option>
                                                        <option v-for="item in xfields" :value="item.xaxis"
                                                            :key="item.xaxis">
                                                            {{item.displayName}}
                                                        </option>
                                                    </select>
                                                    <span name="selectedChartXAxis" v-if="meta.touched && !meta.valid"
                                                        class="invalid-feedback">{{
                                                            errorMessage
                                                        }}</span>
                                                </div>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>{{ $t('YAXIS') }}<span class="text-danger">*</span></label>
                                            <div class="input-group">
                                                <Field name="selectedChartYAxis" :rules="{ 'required': true }" v-model="selectedChartYAxis"
                                                    label="Y Axis" v-slot="{ field, errorMessage, meta }">
                                                    <select v-bind="field" @change="CheckFieldType(selectedChartYAxis)"
                                                        :class="{ 'form-control': true, 'is-invalid': meta.touched && !meta.valid && !selectedChartYAxis }">
                                                        <option value="" disabled>Select</option>
                                                        <option v-for="item in yfields" :key="item" :value="item.yaxis"
                                                          >
                                                          {{item.displayName}}
                                                        </option>
                                                    </select>
                                                    <span name="selectedChartYAxis" v-if="meta.touched && !meta.valid"
                                                        class="invalid-feedback">
                                                        {{ errorMessage }}
                                                    </span>
                                                </Field>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-lg-12">
                                        <div v-if="isTypeInteger" class="form-group">
                                            <label>{{ $t('ViewAs') }}:</label>
                                            <div class="input-group">
                                                <input name="sum" v-model="aggregateFunctionType" value="Sum"
                                                    type="radio" class="mr-2" />{{ $t('Sum') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Count"
                                                    type="radio" class="mr-2 ml-3" />{{ $t('Count') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Average"
                                                    type="radio" class="mr-2 ml-3" />{{ $t('Average') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Minimum"
                                                    type="radio" class="mr-2 ml-3" />{{ $t('Min') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Maximum"
                                                    type="radio" class="mr-2 ml-3" />{{ $t('Max') }}
                                            </div>
                                        </div>
                                    </div>

                                </div>
                                <div class="col-sm-12 col-lg-6 p-3 bg-light border">
                                    <div class="heading-border">
                                        <h5 class="h5"><span>{{ $t('Preview') }}</span></h5>
                                    </div>
                                    <span class="text-center my-3 w-100 float-left">{{ reportTitle }} -
                                        <a href="javascript:void(0)" type="button" class="btn btn-info" @click="ViewReportGraph">
                                            {{ $t('ViewReport') }}
                                        </a>
                                    </span>
                                    <div v-if="PreviewChart" class="col-sm-12 col-lg-12 px-0 py-4 float-left bg-white">
                                        <VueApexCharts :type="selectedChartType" :options="chartOptions"
                                            :series="chartseries">
                                        </VueApexCharts>
                                        <div class="text-center" v-if="aggregateFunctionResult != 0">
                                            <a href="#">{{ resultMsg }} : {{ aggregateFunctionResult }}</a>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </Form>
                    </div>
                    <div class="col-12 p-0 mt-3 float-left">
                        <div class="row flex-row-reverse">
                            <div class="col-lg-6 text-right">
                                <a class="btn btn-success ancsaveuserticket" @click="ApplyGraph">
                                    <em class="fa fa-share pr-2"></em>{{ $t('Apply') }}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <loader :is-visible="isLoading"></loader>
        </div>  
    </div>      
    </template>
<script>
import DataService from '../../services/DataService';
import VueApexCharts from "vue-apexcharts";
export default {
    components: {
        VueApexCharts
    },
    props:{
        selectedLayout:null       
    },
    data: function () {           
        return {
            Columnlist: [],
            GraphJson: [],
            RequestFilterJosn: [],
            Filterlist: [],
            ExpressionArray: [],
            ExpressionFieldsArray: [],
            reportTitle: '',
            chartTypes: null,
            isTypeInteger: false,
            // Selected Chart Properties
            selectedChartTypeID: null,
            selectedChartXAxis: "",
            selectedChartYAxis: "",
            PreviewChart: false,
            aggregateFunctionType: null,
            aggregateFunctionResult: 0,
            //Chart Properties
            chartseries: [],
            chartOptions: {},
            colourList: ['#8892d0', '#da9c49', '#94c5a3', '#e67d21', '#787878', '#c59e71', '#38b8df', '#f55e1a', '#207a92', '#13599c'],
            // Properties for Binding the Chart Valus
            chartOptionXAxisData: [],
            chartSeriesData: [],
            selectedChartType: "",
            resultMsg: "",
            xfields:[],
            yfields:[],
            GraphData:[],
           GroupingGraphData: []
        }
    },
    created(){        
        this.GetChartFileds();
        console.log(this.selectedLayout)
        var vm = this;
        vm.GetChartType();
        vm.reportTitle = vm.selectedLayout.reportName;
        this.GetReportDetailsByReportId();
    },
    
    mounted() {
        $('#modalprojectquickviewpop').modal({ backdrop: 'static', keyboard: false })
        $("#modalprojectquickviewpop").modal('show');         
    },
    methods: {
        async GetReportDetailsByReportId() {
            var vm = this;
            //var params = `ReportId=${vm.selectedLayout.reportId}&IsPaged=1`;
            var params = `ReportId=6170&IsPaged=1`;
            await DataService.GetReportDetailsbyId(params).then(response => {
                if (response.data != null && response.data != undefined) {
                    vm.BindFeilds(response.data.JSON_To_Create_ReportListing[0].Info.FIELDS);
                    vm.BindFilters(response.data.JSON_To_Create_ReportListing[0].Info.FILTERS);
                    vm.BindGROUP(response.data.JSON_To_Create_ReportListing[0].Info.GROUP);
                    vm.sortingData = response.data.JSON_To_Create_ReportListing[0].Info.SORTING
                    vm.graphData = response.data.JSON_To_Create_ReportListing[0].Info.GRAPH;
                    vm.ExpressionArray = response.data.JSON_To_Create_ReportListing[0].Info.EXPRESSION;
                    vm.ExpressionFieldsArray = response.data.JSON_To_Create_ReportListing[0].Info.EXPRESSIONFIELDSARRAY;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        ApplyGraph() {
            var vm = this;
            var graphParams = {
                "selectedChartType": vm.selectedChartType,
                "selectedChartTypeID": vm.selectedChartTypeID,
                "chartseries": vm.chartseries,
                "chartoptions": vm.chartOptions,
                "aggregateFunctionType": vm.aggregateFunctionType,
                "aggregateFunctionResult": vm.aggregateFunctionResult,
                "selectedChartXAxis": vm.selectedChartXAxis,
                "selectedChartYAxis": vm.selectedChartYAxis
            }
            vm.$parent.DrawGraph(graphParams)
            vm.CloseGraphOptions();
        },
        BindFeilds(FIELDS) {
            var vm = this;
            FIELDS.forEach((item) => {
                vm.Columnlist.push(item);
            })       
        },
        BindFilters(FILTERS) {
            var vm = this;
            if (FILTERS.length > 0) {
                vm.filtertype = FILTERS[0].FilterTypeAndorOr;
                vm.bindfilterdata(FILTERS);
                vm.RequestFilterJosn = FILTERS.map((item, index) => {
                    let indexFound = vm.Filterlist.findIndex(field => { return field.CustomFieldId == item.CustomFieldId })
                    let returnObject = {}
                    if (indexFound != -1) {
                        returnObject = {
                            CustomFieldId: item.CustomFieldId,
                            FieldName: vm.Filterlist[indexFound].FieldName,
                            ActualDataType: item.ActualDataType,
                            FilterCount: index + 1,
                            OperatorExpression: item.OperatorExpression,
                            FilterTypeAndorOr: item.FilterTypeAndorOr,
                            OPERATOR_ID: item.OPERATOR_ID,
                            Value1: item.Value1,
                            Value2: item.Value2,

                        }
                    }
                    return returnObject;
                })
            }
        },
        BindGROUP(GROUP) {
            var vm = this;
            GROUP.forEach((item) => {
                vm.Grouplist.push(item);
            })
        },
        PreviewJson: async function (BindGraph) { 
            var vm = this;
            var sortingArray = [];
            var temp;
            if (BindGraph) {
                temp = {
                    "Info": {
                        "REPORTID": vm.selectedLayout.reportId,
                        "REPORTNAME": vm.selectedLayout.reportName,
                        "MODULENAME": 'CRM',
                        "SUBMODULECODE": 'CRM_LEADS',
                        "SUBMODULEID": '',
                        "PAGESIZE": 10,
                        "PAGENUMBER": 1,
                        "IS_PAGED": 1,
                        "FIELDS": vm.Columnlist,
                        "FILTERS": vm.RequestFilterJosn,
                        "GROUP": vm.Grouplist,
                        "SORTING": sortingArray,
                        "GRAPH": [],
                        "EXPRESSION": vm.ExpressionArray,
                        "EXPRESSIONFIELDSARRAY": vm.ExpressionFieldsArray
                    }
                }
            }
            vm.showReportPreview = true;
            vm.saveJsonReport = temp;
            var jsondata = JSON.stringify(temp);
            console.log('jsondata', jsondata);
            await DataService.PreviewReport(jsondata).then((response) => {
                if (BindGraph) {
                    
                    vm.GraphJson = JSON.parse(response.data[0].Value);
                    vm.resultMsg = vm.aggregateFunctionType;
            vm.PreviewChart = false;
            
             if (vm.GraphJson.MetaData[0].IsGrouping) {
                vm.SetGroupingGraphData();
            }
            else {
                vm.GroupingGraphData = vm.GraphJson.ListingData;

            }
            vm.selectedChartType = vm.chartTypes.find(x => x.id == vm.selectedChartTypeID).chart_Type.toLowerCase();
            var xFieldName=vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName;
            var yFieldName=vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName;
            vm.SetGraphValues(vm.GroupingGraphData,xFieldName,yFieldName);
            vm.CalculatingAggregateFuncResult(vm.aggregateFunctionType);
            vm.DrawGraph();
            vm.PreviewChart = true;
                }               
            }).catch((err) => {
                console.log("Data not Found");
            })
        },
        CloseGraphOptions: function () {
            var vm = this;
            var vm = this;
            $("#modalprojectquickviewpop").modal("hide");
            $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();
        },
        GetChartType() {
            var vm = this;
             DataService.GetChartType().then((response) => {
                
                vm.chartTypes = response.data;
               
            })
        },
        GetChartFileds(){
            var vm = this;
            var url=`ReportId=${vm.selectedLayout.reportId}`
             DataService.GetChartFileds(url).then((response) => {
               
               console.log(response.data)
                vm.xfields = response.data.xaxis;
                vm.yfields = response.data.yaxis;
            })
        },
        ViewReportGraph:async function() {            
          var vm = this;
          vm.selectedChartXAxis=10040;
          vm.selectedChartYAxis=10045;
          var BindGraph = true;
          await vm.PreviewJson([], BindGraph);            
        },
        SetGroupingGraphData() {
            var vm = this;
            let ListingData = vm.GraphJson.ListingData;
            let setGraphArray = [];
            let XaxisField =vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName;
            let yAxisField =vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName;
            if (Object.keys(ListingData[0])[0] != undefined && Object.keys(ListingData[0])[0] != '' && vm.GraphData.length > 0) {
                XaxisField = Object.keys(ListingData[0])[0];
                yAxisField = Object.keys(ListingData[0].data[0])[0];
                vm.aggregateFunctionType = vm.GraphData[0].AggregateFuncField;
            }
            ListingData.forEach((item, mainindex) => {
                if (item[XaxisField] != undefined) {
                    if (vm.aggregateFunctionType == "Count") {
                        var pushitem = item[XaxisField] + ` (${item.totalRecord})`;
                        for (var i = 0; i <= parseInt(item.totalRecord); i++) {
                            if (i == 1) {
                                setGraphArray.push({
                                    [XaxisField]: pushitem,
                                    [yAxisField]: item.totalRecord
                                });
                            }
                        }
                    }
                    else {
                        item.data.forEach((item1, dataindex) => {
                            if (dataindex == 0) {
                                var pushitem = item[XaxisField]
                                setGraphArray.push({
                                    [XaxisField]: pushitem,
                                    [yAxisField]: item1[yAxisField]
                                });
                            }
                            else {
                                setGraphArray.push({
                                    [yAxisField]: item1[yAxisField]
                                });
                            }
                        })
                    }
                }
                else {
                    item.data.forEach((item1, dataindex) => {
                        setGraphArray.push({
                            [XaxisField]: item1[XaxisField],
                            [yAxisField]: item1[yAxisField]
                        });
                    })
                }
            })
            vm.GroupingGraphData = setGraphArray
        },
        DrawGraph: function () {
            var vm = this;
            vm.GenrateRandomColorList();
            if (vm.selectedChartType == "bar" || vm.selectedChartType == "line") {
                vm.chartseries = [{
                    data: vm.chartSeriesData
                }]
                vm.chartOptions = {
                    xaxis: {
                        categories: vm.chartOptionXAxisData
                    },
                    dataLabels: {
                        enabled: true,
                    },
                }
            }
            if (vm.selectedChartType == "pie" || vm.selectedChartType == "donut") {
                vm.chartseries = vm.chartSeriesData;
                vm.chartOptions = {
                    labels: vm.chartOptionXAxisData,
                    colors: vm.colourList,
                }
            }
        },
        // Calculating  Aggregate Function Result
        CalculatingAggregateFuncResult: function (functionType) {
            var vm = this;
            if (functionType == "Sum") {
                var sum = 0;
                vm.chartSeriesData.forEach(item => {
                    sum += parseInt(item);
                });
                vm.aggregateFunctionResult = sum;
            }
            if (functionType == "Count") {
                var aggregatecount = 0;
                aggregatecount = vm.chartSeriesData.length;
                vm.aggregateFunctionResult = aggregatecount;
            }
            if (functionType == "Average") {
                var total = 0;
                var count = 0;

                vm.chartSeriesData.forEach(function (item, index) {
                    total += item;
                    count++;
                });
                vm.aggregateFunctionResult = total / count;
            }
            if (functionType == "Minimum") {
                vm.aggregateFunctionResult = Math.min.apply(Math, vm.chartSeriesData.filter(Number));
            }
            if (functionType == "Maximum") {
                vm.aggregateFunctionResult = Math.max(...vm.chartSeriesData)
            }
        },

        // Check field type for View AS checkbox 
        CheckFieldType: function (CustomFieldId) {
            var vm = this;
            vm.isTypeInteger = false;
            var selectedfield = vm.yfields.find(i=>i.yaxis== CustomFieldId);
            if (selectedfield && (selectedfield.dataType == "Decimal" || selectedfield.dataType == "Int")) {
                vm.isTypeInteger = true;
            }
        },

        // Filter column List baesd on CustomFieldId
        FilterColumnlist(CustomFieldId) {
            var vm = this;
            let CustomFieldName, ColumnFieldType;
            for (let [index] of vm.Columnlist.entries())
            {
                if (vm.Columnlist[index].CustomFieldId == CustomFieldId) {
                    if (vm.Columnlist[index].DisplayName != null && vm.Columnlist[index].DisplayName != '') {
                        CustomFieldName = vm.Columnlist[index].DisplayName;
                    }
                    else {
                        CustomFieldName = vm.Columnlist[index].FieldName;
                    }
                    ColumnFieldType = vm.Columnlist[index].ColumnFieldDataCode;
                }else{

                }
            }
            return { 'CustomFieldName': CustomFieldName, 'ColumnFieldType': ColumnFieldType }
        },

        // Set the Chart Xaxis and Yaxis
        SetGraphValues(data, XFieldName, YFieldName) {
            var vm = this;
            vm.chartOptionXAxisData = [];
            vm.chartSeriesData = [];
            data.forEach(items => {
                if (XFieldName in items) {
                    if (items[XFieldName] != null && items[XFieldName] != undefined) {
                        vm.chartOptionXAxisData.push(items[XFieldName])
                        //set graph Values if XField  is Not Null  
                        vm.BindXYFields(items, XFieldName, YFieldName);
                    }
                    else {
                        //set graph Values if XField  is  Null
                        if (items[YFieldName] != null && items[YFieldName] != undefined) {
                            if (typeof XFieldName == "string") {
                                vm.chartOptionXAxisData = [...vm.chartOptionXAxisData, "-"]
                            }
                            else {
                                vm.chartOptionXAxisData = [...vm.chartOptionXAxisData, 0]
                            }
                            vm.BindXYFields(items, XFieldName, YFieldName);
                        }

                    }
                }
            });
        },
        //set graph Values if XField  is Not Null  
        BindXYFields: function (items, XFieldName, YFieldName) {
            var vm = this;
            if (YFieldName in items) {
                if (items[YFieldName] != null && items[YFieldName] != undefined) {
                    if (typeof items[YFieldName] == "string") {
                        var replaceXCurrencySymbol = items[YFieldName].replace(/[$€£]/g, '')
                        vm.chartSeriesData.push(parseInt(replaceXCurrencySymbol))
                    }
                    else {
                        vm.chartSeriesData.push(items[YFieldName])
                    }
                }
                else {
                    vm.chartSeriesData = [...vm.chartSeriesData, 0]
                }
            }
        },

        //Genrate Random Color Code
        GenrateRandomColorCode() {
            const randomColor = Math.floor(Math.random() * 16777215).toString(16);
            return "#" + randomColor;
        },

        // Genrate List of random colors
        GenrateRandomColorList() {
            var vm = this;
            while (vm.colourList.length < vm.chartSeriesData.length) {
                let color;
                color = vm.GenrateRandomColorCode();
                let IsExist = vm.colourList.find(x => x == color)
                if (!IsExist) { vm.colourList.push(color); }
            }
        }
    
}
}
</script>
