<template>
    <div :id="'DonutDiv_'+moduleName+'_'+action+index" class="w-100 bg-white t-min-height donut custom-chart-div text-center">
    <div v-bind:class="(DonutWidgetData.length > 0)?'d-inline-block mt-2 reportBlock align-center w-60 c3':'d-inline-block mt-2 align-center w-60 c3'" :controller="moduleName"  
         :id="'div_WidgetChart_'+action+index"  v-bind:data-test="DonutWidgetData.length" v-bind:style="(DonutWidgetData.length > 0)?'width:60%; float:left; text-align:right;':'width:100%;text-align:center;'">
        <span class="text-danger no-record">{{$t('NoRecordfound')}}</span>
    </div>
    <div :class="'containerLegend_'+ action+ index+' containerLegend100 uniquefixHS custom-chart-legends text-left'" v-show="DonutWidgetData.length > 0"></div>

</div>
</template>
<script>
import "d3";
import 'c3';
import DataService from '../../../services/DataService';
export default {
    props:{
        reportName:null,
        chartType:null,
        moduleName:null,
        fromDate:null,
        toDate:null,
        index:null
    },
    data() {
        return { 
            action:'Lead_By_Product', 
            charT_TYPE:'donut',   
            DonutWidgetData: [],
            HeaderData: null,
            showAddDashboardReport: false,
            ChartName: null
      }  },

        created: function () {
            var vueObj = this;
            this.FetchData();
        },
        methods: {
            FetchData: function () {
               var vueObj = this;
              
                // var url = `moduleName=${vueObj.moduleName}&reportName=Lead_By_Product&fromDate=2023-01-01&toDate=2023-12-31&usersDash=`;
                var url = `moduleName=${vueObj.moduleName}&reportName=CampaignData&&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&usersDash=`;
                DataService.GetDashboardData(url)
                    .then(function (response) {
                     if (response.data != null && response.body != "") {
                        if (response.data.status == "Success") {
                            if (response.data.result != null) {
                                vueObj.DonutWidgetData = response.data.result.data;
                                vueObj.bindWidget(vueObj);
                                }}
                        }
                    });
            },
            ResizeReportWidget: function (zoom, reportName) {
             var name =  '#div_WidgetChart_' + this.action+this.index;
                var chart = $(name).data('c3-chart');
                chart.revert();
                if (zoom == "1") {
                    chart.resize({ height: 200, width: 440 })
                }
                else {
                    if ($(window).width() <= 1280 && $(window).height() <= 1024) {
                        chart.resize({ height: 350, width: 400 })
                    }
                    else {
                        chart.resize({ height: 500, width: 700 })
                    }
                }
            },
            bindWidget: function (vueObj) {
               
                if (vueObj.charT_TYPE == "donut" && vueObj.DonutWidgetData.length > 0) {
                    var chartType = "donut";
                    var arrAUP = [];
                    $.each(vueObj.DonutWidgetData, function (index, element) {
                        if (vueObj.action == "LoggedHoursByProject") {
                            arrAUP.push([element.NAME + " (" + element.VALUE + " h)", element.VALUE]);
                        }
                        else {
                            arrAUP.push([element.NAME + " (" + element.VALUE + ")", element.VALUE]);
                        }
                    });
                    var xCategory = "";
                    var yCategory = "";
                    let box = document.querySelector('#div_WidgetChart_' + vueObj.action+vueObj.index);
                    let width = box.offsetParent.offsetWidth-80;
                    let height = box.offsetParent.offsetHeight-80;
                 var chartHeight = height;
                 var chartWidth = width;
 
                    
                    // if (chartType == "donut") {
                    //     chartWidth = 440;
                    //     chartHeight = 200;
                    // }
                    // else if (chartType == "pie") {
                    //     chartWidth = 480;
                    //     chartHeight = 200;
                    // }
                    //if ($(window).width() <= 1024) {
                    //    chartHeight = 120;
                    //    chartWidth = 120;
                    //}
                    if (vueObj.action == "ResponseOverDue") {
                        xCategory = vueObj.$t('User');
                        yCategory =vueObj.$t('Tickets');
                    }
                    else if (vueObj.action == "EventTicketsSoldReport") {
                        xCategory = vueObj.$t('Date');
                        yCategory = vueObj.$t('Tickets');
                    } else if (vueObj.action == "EventBudgetReport") {
                        xCategory = vueObj.$t('Category');
                        yCategory = vueObj.$t('Budget');
                    } else if (vueObj.action == "EventExpensesReport") {
                        xCategory = vueObj.$t('Category');
                        yCategory = vueObj.$t('Expense');
                    }
                    var selector = '#div_WidgetChart_' + vueObj.action+vueObj.index;
                    $(selector).html('');
                    var Donutchrt = c3.generate({
                        bindto: selector,
                        data: {
                            columns: arrAUP,
                            type: chartType,
                            empty: {
                                label: {
                                    position: "right",
                                    text: vueObj.$t("NoRecordfound")
                                }
                            },
                        },
                        pie: {
                            label: {
                                format: function (value, ratio, id) {
                                    return d3.format('')(value);
                                }
                            }
                        },
                        size: {
                            width: chartWidth,
                            height: chartHeight
                        },
                        legend: {
                            show: true,
                               position: 'bottom',
                        },
                        color: { pattern: ["#23A4FF", "#FF6B6B", "#5FB49C", "#776274", "#916953", "#28587B", "#F4B701", "#272635", "#AEE5D8", "#FFC482", "#3C6E71", "#9FA0FF", "#D1B1CB", "#95B2B8", "#EF8354", "#72D9C8", "#C6EBBE", "#56203D", "#A09CB0", "#5C573E"] },
                        axis: {
                            rotated: false,
                            x: {
                                label: {
                                    text: '',
                                    position: 'outer-center',
                                },
                                categories: [xCategory],
                                type: 'category'
                            },
                            y: {
                                label: {
                                    text: yCategory,
                                    position: 'outer-center'
                                }
                            }
                        }
                    });
                    $(selector).data('c3-chart', Donutchrt);
                    // $('.containerLegend_' + vueObj.action+ vueObj.index).html('');
                    // d3.select('.containerLegend_' + vueObj.action+ vueObj.index).insert('div', '.chart').attr('class', 'legend').selectAll('div')
                    //     .data(arrAUP.map(function (item) { return item[0] }))
                    //     .enter().append('div')
                    //     .attr('data-id', function (id) {
                    //         return id;
                    //     })
                    //     .html(function (id) {
                    //         return id;
                    //     })
                    //     .each(function (id) {
                    //         d3.select(this).append('span').style('background-color', Donutchrt.color(id));
                    //     })
                    //     .on('mouseover', function (id) {
                    //         Donutchrt.focus(id);
                    //     })
                    //     .on('mouseout', function (id) {
                    //         Donutchrt.revert();
                    //     })
                    //     .on('click', function (id) {
                    //         $(this).toggleClass("c3-legend-item-hidden")
                    //         Donutchrt.toggle(id);
                    //     });
                    //}

                }
            },
   
   

    removeReportfromOpenReports(reportId, report) {
        vm.isLoading=true;
        DataService.ReportsRemove('reportId='+reportId)
        .then(function(response){
            if (response.data) {
                //  var paths = getRootURL();
                var report = $(this).parents('div.reportcontainer');
                $(report).remove();
                $(report).show();
            } else {
                vm.ShowAlert(vm.$t(InsertUpdateError), "failure", true, vm.$t("Alert"));
                vm.isLoading=false;
            }

        })

    },

}
}
</script>
<style src="c3/c3.css"></style>