<template>
    
    <div :id="'pieDiv_'+moduleName+'_'+action+index" class="w-100 bg-white custom-chart-div t-min-height piechrt text-center">
    <div v-bind:class="{'d-inline-block mt-2 reportBlock  align-center c3 w-60':PieWidgetData.length > 0,'d-inline-block mt-2 align-center c3 w-60':PieWidgetData.length==0}" :controller="moduleName" 
        :id="'div_WidgetChart_'+action+index" 
        v-bind:style="{'width:60%; float:left; text-align:center;':PieWidgetData.length > 0,'width:100%;text-align:center;':PieWidgetData.length==0}">
        <span class="text-danger no-record">{{$t('NoRecordfound')}}</span>
    </div>
    <div :class="'containerLegend_'+ action+ index+' containerLegend100 uniquefixHS custom-chart-legends text-left'" v-show="PieWidgetData != null && PieWidgetData.length > 0"></div>
</div>
</template>

<script>
import "d3";
import 'c3';
import 'c3/c3.min.css';
import DataService from '../../../services/DataService';
export default {
    props:{
        reportName:null,
        chartType:null,
        moduleName:null,
        fromDate:null,
        toDate:null,
        index:null
    },
    data() {
        return { 
            action:this.reportName, 
            charT_TYPE:'pie',            
            PieWidgetData: [],
            HeaderData: null,
            showAddDashboardReport: false,
        }
    },
    created:function(){
       this.FetchData();
    },
    methods:{
        FetchData: function () {
            var vueObj = this;
            // var params = `moduleName=${vueObj.moduleName}&reportName=Lead_By_Industry&fromDate=2023-03-20&toDate=2023-03-26&usersDash=`;
            var params = `moduleName=${vueObj.moduleName}&reportName=${vueObj.reportName}&&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&usersDash=`;
            DataService.GetDashboardData(params).then(function (response) {
            if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        // if (vueObj.widgetInfo.charT_TYPE == "table" && response.data.result != null) {
                        //     vueObj.HeaderData = response.data.result.headers;
                        // }
                        if (response.data.result != null) {
                            vueObj.PieWidgetData = response.data.result.data;
                            vueObj.bindWidget(vueObj);
                        }
                    }
                }
            });
        },
        ResizeReportWidget: function (zoom, reportName) {
            // debugger;
            var name = '#div_WidgetChart_' + this.action+this.index;
            var chart = $(name).data('c3-chart');
            chart.revert();
            if (zoom == "1") {
                chart.resize({ height: 200, width: 480 })
            }
            else {
                if ($(window).width() <= 1280 && $(window).height() <= 1024) {
                    chart.resize({ height: 350, width: 400 })
                }
                else {
                    chart.resize({ height: 500, width: 700 })
                }
            }
        },
        bindWidget: function (vueObj) {
                    if (vueObj.charT_TYPE == "pie" && vueObj.PieWidgetData.length > 0) {
                       var chartType = "pie";
                       var arrAUP = [];
                       $.each(vueObj.PieWidgetData, function (index, element) {
                           arrAUP.push([element.NAME + " (" + element.VALUE + ")", element.VALUE]);
                       });
                       var xCategory = "Value 1";
                       var yCategory = "Value 2";
                       let box = document.querySelector('#div_WidgetChart_' + vueObj.action+vueObj.index);
                    //    let width = box.offsetParent.offsetWidth-80;
                    //    let height = box.offsetParent.offsetHeight-80;
                    //    var chartHeight =width;// 200;
                    //    var chartWidth = height;//200;
                       if (vueObj.action == "ResponseOverDue") {
                           xCategory = vueObj.$t('User');
                           yCategory = vueObj.$t('Tickets');
                       }
                       if (vueObj.action == "Lead_By_Industry") {
                           xCategory = vueObj.$t('User');
                           yCategory = vueObj.$t('Tickets');
                       }
                       else if (vueObj.action == "EventTicketsSoldReport") {
                           xCategory = vueObj.$t('Date');
                           yCategory = vueObj.$t('Tickets');
                       } else if (vueObj.action == "EventBudgetReport") {
                           xCategory = vueObj.$t('Category');
                           yCategory = vueObj.$t('Budget');
                       } else if (vueObj.action == "EventExpensesReport") {
                           xCategory = vueObj.$t('Category');
                           yCategory = vueObj.$t('Expense');
                       }
                       var selector = '#div_WidgetChart_' + vueObj.action + vueObj.index;
                       $(selector).html('');
                       let c3Obj = {
                           bindto: selector,
                           data: {
                               columns: arrAUP,
                               type: 'pie',
                               empty: {
                                   label: {
                                       position: "right",
                                       text:vueObj.$t("NoRecordfound")
                                   }
                               },
                           },
                           pie: {
                               label: {
                                   format: function (value, ratio, id) {
                                       return d3.format('')(value);
                                   }
                               }
                           },
                        //    size: {
                        //        width: chartWidth,
                        //        height: chartHeight
                        //    },
                           legend: {
                               show: true,
                               position: 'bottom',
                           },
                           color: {  pattern: ["#23A4FF", "#FF6B6B", "#5FB49C", "#776274", "#916953", "#28587B", "#F4B701", "#272635", "#AEE5D8", "#FFC482", "#3C6E71", "#9FA0FF", "#D1B1CB", "#95B2B8", "#EF8354", "#72D9C8", "#C6EBBE", "#56203D", "#A09CB0", "#5C573E"] },
                           axis: {
                               rotated: false,
                               x: {
                                   label: {
                                       text: '',
                                       position: 'outer-center',
                                   },
                                   categories: [xCategory],
                                   type: 'category'
                               },
                               y: {
                                   label: {
                                       text: yCategory,
                                       position: 'outer-center'
                                   }
                               }
                           }
                       }
                       var Piechrt = c3.generate(c3Obj);
                       $(selector).data('c3-chart', Piechrt);
                    //    $('.containerLegend_' + vueObj.action+ vueObj.index).html('');
                    //    d3.select('.containerLegend_' + vueObj.action+ vueObj.index).insert('div', '.chart').attr('class', 'legend').selectAll('div')
                    //        .data(arrAUP.map(function (item) { return item[0] }))
                    //        .enter().append('div')
                    //        .attr('data-id', function (id) {
                    //            return id;
                    //        })
                    //        .html(function (id) {
                    //            return id;
                    //        })
                    //        .each(function (id) {
                    //            d3.select(this).append('span').style('background-color', Piechrt.color(id));
                    //        })
                    //        .on('mouseover', function (id) {
                    //            Piechrt.focus(id);
                    //        })
                    //        .on('mouseout', function (id) {
                    //            Piechrt.revert();
                    //        })
                    //        .on('click', function (id) {
                    //            $(this).toggleClass("c3-legend-item-hidden")
                    //            Piechrt.toggle(id);
                    //        });
                       //}                                 
                   }
               },
        },
    }

</script>
<style src="c3/c3.css"></style>
<style>
    .custom-chart-div.Barchrt .reportBlock {
        transform: initial;
        height: inherit;
    }
</style>