<template>
    <grid-layout :layout.sync="layout" :col-num="12" :row-height="30" :is-draggable="draggable" :is-resizable="resizable"
        :vertical-compact="true" :use-css-transforms="true">
        <grid-item v-for="item in layout" :static="item.static" :x="item.x" :y="item.y" :w="item.w" :h="item.h" :i="item.i" @resized="resizedEvent(item)">
            <div :class="{ 'graph-widget align-self-stretch ticketingDashboardHead reportcontainer mb-2': true }" >
                <div class="dashboard-boxes graph-widget-box bg-white">
                    <div class="graph-widget-heading float-left w-100">
                        <span class="text-truncate float-left w-75">{{ item.reportName }}</span>
                        <div class="mw-80 float-right">
                            <a href="javascript:;" class="hlkDeleteReport float-right" title="Remove"
                                @click="removeItem(item.i)">
                                <span class="icon_close">
                                    <i class="fa fa-times"></i>
                                </span>
                            </a>
                            <!-- <a href="javascript:;" class="zoomin rptexpand ancdashboard float-right" @click="ExpandCollapseReport($event,item)" >
                                    <span class="icon_expand">
                                        <i class="fa fa-expand"></i>
                                    </span>
                                </a> -->
                            <a href="javascript:;" @click="openWidgetSetting(item)" class="float-right"><span
                                    class="icon_expand"><i class="fa fa-cog"></i></span></a>
                        </div>
                    </div>
                    <div
                        :class="{ 'raph-widget-content bg-white float-left w-100': true, 'd-flex  align-content-center justify-content-center': true }">
                        <!-- <Skeletor  v-if="layout"/>   -->
                        <bar-chart v-if="item.chartType && item.chartType == 'Bar'"
                            :ref="'bar' + item.reportId + '_' + item.widgetId" :fromDate="fromDate" :toDate="toDate"
                            moduleName="CRM" :index="item.i" :reportName="item.reportName"
                            :chartType="item.chartType"></bar-chart>
                        <PieChart v-if="item.chartType && item.chartType == 'Pie'"
                            :ref="'pie' + item.reportId + '_' + item.widgetId" :fromDate="fromDate" :toDate="toDate"
                            moduleName="CRM" :index="item.i" :reportName="item.reportName" :chartType="item.chartType" width="" height="">
                        </PieChart>
                        <DonutChart v-if="item.chartType && item.chartType == 'Donut'"
                            :ref="'donut' + item.reportId + '_' + item.widgetId" :fromDate="fromDate" :toDate="toDate"
                            moduleName="CRM" :index="item.i" :reportName="item.reportName" :chartType="item.chartType">
                        </DonutChart>
                        <TableChart v-if="!item.chartType" :fromDate="fromDate"
                            :ref="'table' + item.reportId + '_' + item.widgetId" :toDate="toDate" moduleName="Project"
                            :index="item.i" :reportName="item.reportName" :chartType="item.chartType"></TableChart>
                    </div>
                </div>
            </div>
        </grid-item>
    </grid-layout>
</template>

<script>
import BarChart from '../Dashboard/Widget/BarChart.vue';
import PieChart from '../Dashboard/Widget/PieChart.vue';
import DonutChart from '../Dashboard/Widget/DonutChart.vue';
import TableChart from './Widget/TableChart.vue';
import WidgetSetting from './WidgetSetting.vue'
export default {
    components: { BarChart, PieChart, DonutChart, TableChart, WidgetSetting },
    props: {
        reports: null,
        fromDate: null,
        toDate: null
    },
    data: function () {
        return {
            layout: [
                { "x": 0, "y": 0, "w": 2, "h": 2, "i": "0", static: false },
                { "x": 2, "y": 0, "w": 2, "h": 4, "i": "1", static: true },
                { "x": 4, "y": 0, "w": 2, "h": 5, "i": "2", static: false },
                { "x": 6, "y": 0, "w": 2, "h": 3, "i": "3", static: false },
                { "x": 8, "y": 0, "w": 2, "h": 3, "i": "4", static: false },
                { "x": 10, "y": 0, "w": 2, "h": 3, "i": "5", static: false },
                { "x": 0, "y": 5, "w": 2, "h": 5, "i": "6", static: false },
                { "x": 2, "y": 5, "w": 2, "h": 5, "i": "7", static: false },
                { "x": 4, "y": 5, "w": 2, "h": 5, "i": "8", static: false },
                { "x": 6, "y": 3, "w": 2, "h": 4, "i": "9", static: true },
                { "x": 8, "y": 4, "w": 2, "h": 4, "i": "10", static: false },
                { "x": 10, "y": 4, "w": 2, "h": 4, "i": "11", static: false },
                { "x": 0, "y": 10, "w": 2, "h": 5, "i": "12", static: false },
                { "x": 2, "y": 10, "w": 2, "h": 5, "i": "13", static: false },
                { "x": 4, "y": 8, "w": 2, "h": 4, "i": "14", static: false },
                { "x": 6, "y": 8, "w": 2, "h": 4, "i": "15", static: false },
                { "x": 8, "y": 10, "w": 2, "h": 5, "i": "16", static: false },
                { "x": 10, "y": 4, "w": 2, "h": 2, "i": "17", static: false },
                { "x": 0, "y": 9, "w": 2, "h": 3, "i": "18", static: false },
                { "x": 2, "y": 6, "w": 2, "h": 2, "i": "19", static: false }
            ],
            draggable: true,
            resizable: true,
            index: 0
        }
    },
    mounted() {
        this.index = this.layout.length;
    },
    created() {
        this.CreateReportWidget();
        this.isLoading = false
    },
    methods: {
        itemTitle(item) {
            let result = item.i;
            if (item.static) {
                result += " - Static";
            }
            return result;
        },
        ExpandCollapseReport: function (event, item) {
            $(event.target).closest(".vue-grid-item").width(996).height(390);
            //  var vm=this;
            //   var selectedReport= vm.layout.find(i=>i.index==item.index);
            //    selectedReport.x=selectedReport.x+2;
        },
        onResizeStop: function (item) {
            switch (item.chartType) {
                case 'Bar':
                    this.$refs['bar' + item.reportId + '_' + item.widgetId][0].FetchData();
                    break;
                case 'Pie':
                    this.$refs['pie' + item.reportId + '_' + item.widgetId][0].FetchData();
                    break;
                case 'Donut':
                    this.$refs['donut' + item.reportId + '_' + item.widgetId][0].FetchData();
                    break;
                default:
                    this.$refs['table' + item.reportId + '_' + item.widgetId][0].FetchData();
                    break;
            }
        },
        CreateReportWidget() {
            console.log(this.reports);
            var vm = this;
            vm.layout = [];
            for (let index = 0; index < vm.reports.length; index++) {
                vm.layout.push({
                    "reportId": vm.reports[index].reportId, 
                    "widgetId": vm.reports[index].widgetId,
                    "reportName": vm.reports[index].reportName, 
                    "chartType": vm.reports[index].chartType,
                    "x": parseInt(vm.reports[index].xaxis), 
                    "y": parseInt(vm.reports[index].yaxis), 
                    "w": 2, 
                    "h": 10, 
                    "i": index.toString(),
                    static: false,
                    boxWidth: 0,
                    boxHeight: 0,
                })
            }

        },
        increaseWidth: function () {
            let width = document.getElementById("content").offsetWidth;
            width += 20;
            document.getElementById("content").style.width = width + "px";
        },
        decreaseWidth: function () {
            let width = document.getElementById("content").offsetWidth;
            width -= 20;
            document.getElementById("content").style.width = width + "px";
        },
        removeItem: function (i) {
            console.log("### REMOVE " + i);
            const index = this.layout.map(item => item.i).indexOf(i);
            this.layout.splice(index, 1);
        },
        openWidgetSetting(item) {
            this.selectedLayout = item;
            this.showWidgetsetting = !this.showWidgetsetting;
        },
        addItem: function () {
            const item = { "x": 0, "y": 0, "w": 2, "h": 2, "i": this.index + "", whatever: "bbb" };
            this.index++;
            this.layout.push(item);
        },
        resizedEvent: function(i, newX, newY, newHPx, newWPx){
            const msg = "RESIZED i=" + i + ", X=" + newX + ", Y=" + newY + ", H(px)=" + newHPx + ", W(px)=" + newWPx;
            console.log(msg);

        },
    }
}
</script>
<style scoped>
.vue-grid-layout {
    background: #eee;
}

.vue-grid-item:not(.vue-grid-placeholder) {
    background: #ccc;
    border: 1px solid black;
}

.vue-grid-item .resizing {
    opacity: 0.9;
}

.vue-grid-item .static {
    background: #cce;
}

.vue-grid-item .text {
    font-size: 24px;
    text-align: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    height: 100%;
    width: 100%;
}

.vue-grid-item .no-drag {
    height: 100%;
    width: 100%;
}

.vue-grid-item .minMax {
    font-size: 12px;
}

.vue-grid-item .add {
    cursor: pointer;
}

.vue-draggable-handle {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 0;
    left: 0;
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='10' height='10'><circle cx='5' cy='5' r='5' fill='#999999'/></svg>") no-repeat;
    background-position: bottom right;
    padding: 0 8px 8px 0;
    background-repeat: no-repeat;
    background-origin: content-box;
    box-sizing: border-box;
    cursor: pointer;
}
</style>