<template>
    <div class="modal my-popups right fade" data-keyboard="false" data-backdrop="static" id="modalprojectquickviewpop"
        role="dialog" v-cloak>
        <div class="modal-dialog maxwidth500" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" style="height:56px;" class="close" v-on:click="CloseReportView()"
                        data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 class="modal-title text-truncate" style="line-height:26px;" id="myModalLabel">Add Report</h4>
                </div>
                <div class="modal-body">
                    <div class="container-fluid p-0">
                        <h5 >Reports:</h5>
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div id="accordion" role="tablist" class=" accordion md-accordion w-100" v-if="reportGroups">
                                    <div class="card" v-for="(reportGroup, index) in reportGroups">
                                        <div class="card-header"><a class="card-link collapsed" data-toggle="collapse"
                                             data-parent="#accordion"   :href="'#collapseOne'+index"  aria-expanded="true" :data-target="'#collapseOne'+index" :aria-controls="'collapseOne'+index">
                                                <div class=""><span><em class="fa fa-folder rotate-icon pr-1"
                                                            aria-hidden="true"></em>{{ reportGroup.reports[0].folderName }}</span></div>
                                            </a></div>
                                        <div :id="'collapseOne'+index" class="collapse" role="tabpanel"  data-parent="#accordion" style="">
                                            <div class="card-body">
                                                <div class="row" style="max-height: 400px; overflow-y: auto;">
                                                    <div class="col-md-12 listing">
                                                       <div class="table-responsive table-fix-header">

                                                        <table :id="'tblReport'+index" class="table table-bordered mt-0">
                                                            <thead class="thead-bg">
                                                                <tr>
                                                                    <th class="text-center"><input class="chkItems" id="chkAll"  type="checkbox"></th>
                                                                    <th>Report Name</th>
                                                                    <th>View Type</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                <tr v-for="(report,reportIndex) in reportGroup.reports">
                                                                        <td class="text-center"><input class="chkItems" :data-reportid="report.reportId"
                                                                            :data-chartType="report.chartType==null?0:report.chartType"  type="checkbox"
                                                                            :checked="reportbydashbord && reportbydashbord.some(ele=>ele.reportId==report.reportId)"></td>
                                                                        <td>{{ report.reportName }}</td>
                                                                        <td> <label class="switch auto">
                                                                        <input :id="'chartType'+reportIndex+'_'+index" class="form-check-input rdoticketfor custom-control-input dynamic" 
                                                                        type="checkbox" v-bind:name="'viewtype'"
                                                                        v-bind:checked="report.chartType"  v-bind:value="reportbydashbord && reportbydashbord.some(ele=>ele.reportId==report.reportId)? reportbydashbord.find(ele=>ele.reportId==report.reportId).chartType: report.chartType?report.chartType:'Table'">
                                                                        <span class="slider round" >
                                                                            <span class="slider-yes">Graph</span>
                                                                            <span class="slider-no">Table</span> 
                                                                        </span>
                                                                    </label></td>
                                                                    </tr>
                                                            </tbody>
                                                        </table></div></div>
                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-right mt-3">
                      <a href="javascript:;" data-tour="step: 1; title: enter DealDetail; content: AddDealDetailContent" class="btn btn-success float-right" title="Save" v-on:click="saveReport()"><em class="fa fa-save pr-2"></em>Save </a>
                            </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
    <loader :is-visible="isLoading"></loader>
</div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props:{
        dashboardid:null
    },
    data: function () {
        return {
            isLoading: false,
            reportGroups:[],
            reportbydashbord:null

        }
    },
    created(){
        this.GetDynamicReport();
        
    },
    mounted() {
        $("#modalprojectquickviewpop").modal('show');
        this.CheckBoxBootstrap();
    },
    methods: {
        CloseReportView: function () {
            var vm = this;
            vm.$parent.showAddReport = false;
        },
        GetDynamicReport() {
            var vm = this;
            var param = 'moduleName='
            DataService.GetDynamicReport(param).then(function (response) {            
                vm.reportGroups = response.data;
                setTimeout(() => {
                    vm.CheckBoxBootstrap();
                    vm.GetDashboardReport();
                }, 1000);
            })
        },
        GetDashboardReport:function(){
            var vm = this;
            var param =`dashboardId=${vm.dashboardid}`
            DataService.GetDashboardReport(param).then(function (response) {
                vm.reportbydashbord = response.data;
                console.log(vm.reportbydashbord)
              
            })
        },
    
        saveReport: function () { 
             var vm = this;             
                
            // this.isLoading=true;
            var reportArr=[];
            for (let index = 0; index < vm.reportGroups.length; index++) {              
                $('#tblReport'+index+' .chkItems:checked').not("[id^='chkAll']").each(function () {                   
                if ($(this).attr("data-reportid")) {
                   reportArr.push({
                    reportId:$(this).attr("data-reportid"),
                    chartType:$(this).parent().parent().parent().find('.rdoticketfor').is(":checked")?'Graph':'Table',
                    displayOrder:index,
                    displayOrder:index,
                    xaxis:xCount,
                    yaxis:yCount
                   })
                }
            });
            }
           var data = {
                moduleName: 'CRM',
                reports:reportArr
                        };
                         var objectPostString = JSON.stringify(data);
              DataService.SaveDashboard(objectPostString).then(function (response) {
                 
                    if (response.data[0].Status == "Success") {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t('error', [vm.$t('Report')]), "warning", true, vm.$t("Alert"));
                    }
                    vm.isLoading = false;
                });
        
    }
    }
}
</script>
<style>
.switch.auto {
    width: auto !important;
    overflow: unset !important;
}

.switch.auto .slider {
    padding: 2px 7px 0px 8px;
    left: auto !important;
    right: auto !important;
}

.switch.auto input:checked + .slider::before {
    transform: translate(26px);
    left: auto;
    right: 30px;
}
.accordion>.card>.card-header {
    border-radius: 0;
    margin-bottom: 10px;
}
</style>