<template>
    <div :id="'BarChart_'+moduleName+'_'+action+index" class="w-100 bg-white custom-chart-div t-min-height Barchrt text-center">
     <div v-bind:class="{'bar d-inline-block mt-2 reportBlock t-min-height align-center c3 w-100':BarWidgetData.length > 0,'bar d-inline-block mt-2 align-center c3 w-100':BarWidgetData.length==0}" :controller="moduleName" 
         :id="'div_BarChart_'+action+index" 
         v-bind:style="{'width:60%; float:left; text-align:center;':BarWidgetData.length > 0,'width:100%;text-align:center;':BarWidgetData.length==0}">
         <span class="text-danger no-record">{{$t('NoRecordfound')}}</span>
     </div>
 </div>
 </template>
 
 <script>
import "d3";
import 'c3';
 import DataService from '../../../services/DataService';
 export default {
     props:{
         reportName:null,
         chartType:null,
         moduleName:null,
         fromDate:null,
         toDate:null,
         index:null
     },
     data() {
         return {     
             action:'Lead_By_Status', 
             charT_TYPE:'bar',      
             BarWidgetData: [],
             HeaderData: null,
             showAddDashboardReport: false,
         }
     },
     created:function(){
     this.FetchData();
     },
     methods:{
         FetchData: function () {
             var vueObj = this;
             var params = `moduleName=${vueObj.moduleName}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&usersDash=`;
             // var params = `moduleName=${vueObj.moduleName}&reportName=${vueObj.reportName}&fromDate=03-20-2023&toDate=03-26-2023&usersDash=`;
             DataService.GetDashboardData(params).then(function (response) {
                 if (response.data != null && response.data != "") {
                     if (response.data.status == "Success") {
                      if (response.data.result != null) {
                             vueObj.BarWidgetData = response.data.result.data;
                              vueObj.bindWidget(vueObj);
                         }
                     }
                 }
             });
         },
         ResizeReportWidget: function (zoom, reportName) {
             // debugger;
             var name = '#div_BarChart_' + reportName;
             var chart = $(name).data('c3-chart');
             chart.revert();
             if (zoom == "1") {
                 chart.resize({ height: 200, width: 400 })
             }
             else {
                 if ($(window).width() <= 1280 && $(window).height() <= 1024) {
                     chart.resize({ height: 200, width: 300 })
                 }
                 else {
                     chart.resize({ height: 300, width: 800 })
                 }
             }
         },
         bindWidget: function (vueObj) {
           if (vueObj.charT_TYPE == "bar" && vueObj.BarWidgetData.length > 0) {
                 var arrAU = [];
                 var chartType = "bar";
                 $.each(vueObj.BarWidgetData, function (index, element) {
                     arrAU.push([element.NAME, element.VALUE]);
                 });
                 var xCategory = "";
                 var yCategory = "";
                 let box = document.querySelector('#div_BarChart_' + vueObj.action+vueObj.index);
                       let width = box.offsetParent.offsetWidth-80;
                       let height = box.offsetParent.offsetHeight-80;
                 var chartHeight = height;
                 var chartWidth = width;
 
                //  if ($(window).width() <= 1440) {
                //      chartHeight = 120;
                //      chartWidth = 120;
                //  }
                 if (vueObj.action == "ProjectsStatusWise") {
                     xCategory = vueObj.$t('Status');
                     yCategory = vueObj.$t('Projects');
                 }
                 else if (vueObj.action == "Lead_By_Status") {
                     xCategory = vueObj.$t('Status');
                     yCategory = vueObj.$t('Leads');
                 }
                 else {
                     xCategory = vueObj.$t('User');
                     yCategory = vueObj.$t('Tickets');
                 }
                 var selector = '#div_BarChart_' + vueObj.action+vueObj.index;
                 $(selector).html('');
                 var arrayOfLinks = [];
                 arrayOfLinks.push({ name: "Active", url: '/Project/Index?searchCondition=PR.STATUS_ID in(1001)' });
                 arrayOfLinks.push({ name: "Active", url: '/Project/Index?searchCondition=PR.STATUS_ID in(1001)' });
                 var Barchrt = c3.generate({
                     bindto: selector,
                     data: {
                         columns: arrAU,
                         type: chartType,
                         empty: {
                             label: {
                                 position: "right",
                                 text: vueObj.$t("NoRecordfound")
                             }
                         },
                         
                         onclick: function (data) {
                             if (moduleName == "CRM") {
                                 window.open('javascript:;');
                             }
                             else {
                                 window.open('/Project/Index?searchCondition=PR.STATUS_ID in(1001)', '_blank');
                             }
                         },
                     },
                     bar: {
                         space: 0.15,
                         width: {
                             ratio: arrAU.length > 4 ? 0.5 : 0.3
                         }
                     },
                     size: {
                        width: chartWidth,
                       height: chartHeight,
                     },
                     legend: {
                               show: true,
                               position: 'bottom',
                           },
               color: { pattern: ["#23A4FF", "#FF6B6B", "#5FB49C", "#776274", "#916953", "#28587B", "#F4B701", "#272635", "#AEE5D8", "#FFC482", "#3C6E71", "#9FA0FF", "#D1B1CB", "#95B2B8", "#EF8354", "#72D9C8", "#C6EBBE", "#56203D", "#A09CB0", "#5C573E"]},
                     axis: {
                         rotated: false,
                         x: {
                             label: {
                                 text: '',
                                 position: 'outer-center',
                             },
                             categories: [xCategory],
                             type: 'category'
                         },
                         y: {
                             label: {
                                 text: yCategory,
                                 position: 'outer-center'
                             },
                             padding: {
                                 top: 20
                             }
                         }
                     }
                     
                 });
   
                  
                 $(selector).data('c3-chart', Barchrt);
                
                 
                //     $('.containerLegendDue').html('');
                //    d3.select('.containerLegendDue').insert('div', '.chart').attr('class', 'legend').selectAll('div')
                //      .data(arrAU.map(function (item) { return item[0] }))
                //      .enter().append('div')
                //      .attr('data-id', function (id) {
                //          return id;
                //      })
                     
                //      .html(function (id) {
                //          return id;
                //      })
                //      .each(function (id) {
                //          d3.select(this).append('span').style('background-color', Barchrt.color(id));
                //      })
                //      .on('mouseover', function (id) {
                //          Barchrt.focus(id);
                //      })
                //      .on('mouseout', function (id) {
                //          Barchrt.revert();
                //      })
                //      .on('click', function (id) {
                //          //$(this).toggleClass("c3-legend-item-hidden")
                //          Barchrt.toggle(id);
                //      });
                //      setTimeout(() => {
                //         this.Barchrt.style('max-height', 'none')
                //      }, 100);
                //  //}
             }
         },
     }
 }
 </script>
 <style src="c3/c3.css"></style>
 <style>
     .custom-chart-div.Barchrt .reportBlock {
         transform: initial;
     }
     .t-min-height{
        min-height: 200px;
     }
 
 </style>