<template>
    <div :id="'div_'+action+index" class="listing">
     <div class="listing">
         <div class="table-responsive border-0">
             <div class="graph-widget align-self-stretch pl-0">
                 <table class="table table-bordered dt-responsive nowrap mt-0">
                     <thead class="thead-bg">
                         <tr>
                             <th v-for="item in HeaderData" :key="item">
                                 <span>{{item.column_name}}</span>
                             </th>
                         </tr>
                     </thead>
                     <tbody>
                         <tr v-for="(item, index) in OpenTicketsWidgetData" :key="item" :v-if="OpenTicketsWidgetData.length> 0">
                             <td>
                                 <a target="_blank" v-bind:href="item.widgetType=='RecentlyAddedProjects'?'/Project/Index':'/FSM/Index?priorityCode='+item.Name+'&PageSize=10&PageNumber=1'">
                                     <i v-if="item.widgetType!='RecentlyAddedProjects'" v-bind:class="{'fa fa-exclamation text-white':true, 'icon_ticket_Critical legends_Critical':(item.Name == 'Critical'),'icon_ticket_High legends_high':(item.Name == 'High'), 'icon_ticket_Medium legends_medium':(item.Name == 'Medium'), 'icon_ticket_Low legends_low':(item.Name == 'Low'), 'icon_ticket_Critical legends_profile':(item.Name == 'Emergency')}" aria-hidden="true"></i>
                                     <span class="text-dark">{{item.Name}}</span>
                                 </a>
                             </td>
                             <td>
                                 <a target="_blank" v-bind:href="item.widgetType=='RecentlyAddedProjects'?'/Project/Index':'/FSM/Index?priorityCode='+item.Name+'&PageSize=10&PageNumber=1'">
                                     <span v-bind:class="item.widgetType=='RecentlyAddedProjects'?'text-center text-dark':'text-dark float-right'">{{item.Value}}</span>
                                 </a>
                             </td>
                         </tr>
                         <tr v-if="OpenTicketsWidgetData.length == 0"><td colspan="20" class="text-center text-danger">{{$t('NoRecordfound')}}</td></tr>
                     </tbody>
                 </table>
             </div>
         </div>
     </div>
 </div> 
 </template>
 
 <script>
 import DataService from '../../../services/DataService';
 export default {
     props:{
        reportName:null,
        chartType:null,
         moduleName:null,
         fromDate:null,
         toDate:null,
         DashUsers:null
     },
     data: function () {
             return {
                 action:'RecentlyAddedProjects', 
                 charT_TYPE:'table', 
                 OpenTicketsWidgetData: [],
                 HeaderData: null,
                 showAddDashboardReport: false,
             }
         },
         created: function () {
          
             this.fetchRecordsofOpenTickets();
         },
         computed: {
             WidgetData: function () {
                 return this.OpenTicketsWidgetData;
             },
             WidgetHeaderData: function () {
                 return this.HeaderData;
             }
         },
         methods: {
             fetchRecordsofOpenTickets: function () {
                 var vueObj = this;
               var params = `moduleName=Project&reportName=RecentlyAddedProjects&fromDate=2023-03-20&toDate=2023-03-26&usersDash=`;
                 DataService.GetDashboardData(params).then(function (response) {
                      if (response.data != null && response.data != "") {
                             if (response.data.status == "Success") {
                                     vueObj.HeaderData = response.data.result.headers;                                 
                                 if (response.data.result != null) {
                                     vueObj.OpenTicketsWidgetData = response.data.result.data;
                                 }
                             }
                         }
                     });
             }
         },
 }
 </script>