<template>
    <div class="modal my-popups right fade" data-keyboard="false" data-backdrop="static" id="modalprojectquickviewpop"
        role="dialog" v-cloak>
        <div class="modal-dialog maxwidth500" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" style="height:56px;" class="close" v-on:click="CloseDashboardView()"
                        data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 class="modal-title text-truncate" style="line-height:26px;" id="myModalLabel">Add Dashboard</h4>
                </div>
                <div class="modal-body">
                    <div class="container-fluid p-0">

                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="form-group"><label>{{ $t("Dashborad Name") }}:</label>
                                    <input v-bind:class="{ 'form-control': true }" id="dashboradName"
                                        name="dashboradName" maxlength="200" type="text" v-model="dashboradName">
                                </div>
                            </div>
                        </div>
                        
                        <h5 >Reports:</h5>
                     
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div id="accordion" role="tablist" class=" accordion md-accordion w-100" v-if="reportGroups">
                                    <div class="card" v-for="(reportGroup, index) in reportGroups">
                                        <div class="card-header"><a class="card-link collapsed" data-toggle="collapse"
                                             data-parent="#accordion"   :href="'#collapseOne'+index"  aria-expanded="true" :data-target="'#collapseOne'+index" :aria-controls="'collapseOne'+index">
                                                <div class=""><span><em class="fa fa-folder rotate-icon pr-1"
                                                            aria-hidden="true"></em>{{ reportGroup.reports[0].folderName }}</span></div>
                                            </a></div>
                                        <div :id="'collapseOne'+index" class="collapse" role="tabpanel"  data-parent="#accordion" style="">
                                            <div class="card-body">
                                                <div class="row" style="max-height: 400px; overflow-y: auto;">
                                                    <div class="col-md-12 listing">
                                                       <div class="table-responsive table-fix-header">

                                                        <table :id="'tblReport'+index" class="table table-bordered mt-0">
                                                            <thead class="thead-bg">
                                                               
                                                                <tr>
                                                                
                                                                    <th class="text-center"><input class="chkItems" id="chkAll" v-on:change="onChange()"   type="checkbox"></th>
                                                                    <th>Report Name</th>
                                                                    <th>View Type</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                <tr v-for="(report,reportIndex) in reportGroup.reports">
                                                                        <td class="text-center"><input class="chkItems" :data-reportid="report.reportId"
                                                                            :data-chartType="report.chartType==null?0:report.chartType"  type="checkbox"></td>
                                                                        <td>{{ report.reportName }}</td>
                                                                        <td> <label class="switch auto">
                                                                        <input :id="'chartType'+reportIndex+'_'+index" class="form-check-input rdoticketfor custom-control-input dynamic" 
                                                                        type="checkbox" v-bind:name="'viewtype'"
                                                                        v-bind:checked="report.chartType"  v-bind:value="report.chartType?report.chartType:'Table'">
                                                                        <span class="slider round" >
                                                                            <span class="slider-yes">Graph</span>
                                                                            <span class="slider-no">Table</span> 
                                                                        </span>
                                                                    </label></td>
                                                                    </tr>
                                                            </tbody>
                                                        </table>

                                                       </div>
                                                       
                                                       
                                                </div>
                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-right mt-3">
                      <a href="javascript:;" data-tour="step: 1; title: enter DealDetail; content: AddDealDetailContent" class="btn btn-success float-right" title="Save" v-on:click="saveDashboard()"><em class="fa fa-save pr-2"></em>Save </a>
                            </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
    <loader :is-visible="isLoading"></loader>
</div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    data: function () {
        return {
            isLoading: false,
            reportGroups:[]
        }
    },
    created(){
        this.GetDynamicReport();
    },
    mounted() {
        $("#modalprojectquickviewpop").modal('show');         
    },
    methods: {
        CloseDashboardView: function () {
            var vm = this;
            vm.$parent.showAdddashboard = false;
        },
        GetDynamicReport() {
            var vm = this;
            var param = 'moduleName='
            DataService.GetDynamicReport(param).then(function (response) {
            vm.reportGroups = response.data;
                setTimeout(() => {
                    vm.CheckBoxBootstrap();
                    
                }, 1000);
            })
        },
      
        saveDashboard: function () {  
          var vm = this;  
            // this.isLoading=true;
            var reportArr=[];
          
            for (let index = 0; index < vm.reportGroups.length; index++) {                           
                $('#tblReport'+index+' .chkItems:checked').not("[id^='chkAll']").each(function (rowIndex) {                            
                if ($(this).attr("data-reportid")) {
                   reportArr.push({
                    reportId:$(this).attr("data-reportid"),
                    chartType:$(this).parent().parent().parent().find('.rdoticketfor').is(":checked")?'Graph':'Table',
                    displayOrder:index,
                    xaxis:0,
                    yaxis:0
                   })
                }
            });
            }
            var xCount=0,yCount=0;
            for (let index = 0; index < reportArr.length; index++) {
                reportArr[index].xaxis=xCount;
                reportArr[index].yaxis=yCount;
                if(xCount==4){
                    xCount=0;
                    yCount=yCount+2;
                }else{
                    xCount=xCount+2;
                }
            }            
            var data = {
                dashboardName:vm.dashboradName,
                dashboardDesc:'added dashboard',
                moduleName: 'CRM',
                reports:reportArr
                        };
                        vm.isLoading = true;
        var objectPostString = JSON.stringify(data);
        DataService.SaveDashboard(objectPostString).then(function (response) {
            vm.isLoading = false;
        if (response.data.status == "Success") {
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Dashboard')]), "success", true, vm.$t("Alert"));
                    $("#modalprojectquickviewpop").modal('hide'); 
                    vm.$parent.GetDashboard();
                }
                else {
                    vm.ShowAlert(vm.$t('Failure', [vm.$t('Dashboard')]), "warning", true, vm.$t("Alert"));
                }                    
                });        
    }
}
}
</script>
<style>
.card-header > a[aria-expanded="true"]{
    background-color: #56a5fa;
}
.switch.auto {
    width: auto !important;
    overflow: unset !important;
}

.switch.auto .slider {
    padding: 2px 7px 0px 8px;
    left: auto !important;
    right: auto !important;
}

.switch.auto input:checked + .slider::before {
    transform: translate(26px);
    left: auto;
    right: 30px;
}
.accordion>.card>.card-header {
    border-radius: 0;
    margin-bottom: 10px;
}
</style>